import React, { Component } from 'react';
import Swal from 'sweetalert2'

class SweetalertConfirm extends Component {
  deletAlert = (e) => {
    Swal.fire({
      title: ' ?',
      icon: 'question',
      showCancelButton: true,
      confirmButtonColor: '#4B088A',
      cancelButtonColor: '#01DF01',
      confirmButtonText: '',
      cancelButtonText: ''
    }).then((result) => {
      if (result.value) {
        document.getElementById('deleteId').remove();
        Swal.fire(
          'Deleted',
          'sweetalert2 ',
          'success'
        )
      }
    })
  }

  render() {
    return (
      <>
        <h1 id='deleteId'></h1>
        <button onClick={e => this.deletAlert()}></button>
      </>
   )
  }
}

export default SweetalertConfirm;